module.exports = [{"title":"Fandom Quiz","content":["<p>How to know you&rsquo;re ready for Abacus&rsquo; fandom quiz</p><ol><li>Your dream is to become a genius, billionaire, playboy philanthropist.</li><li>You can&rsquo;t not hear this in Hermione&rsquo;s voice &ndash; &lsquo;it&rsquo;s leviosa, not leviosa&rsquo;</li><li>You use so many pop culture references that no one knows what you&rsquo;re talking about anymore. (YOLO right?)</li></ol><p>Ready to be Sherlocked?</p>","<p><strong>SERIES :</strong></p><ul><li>Game of thrones</li><li>Stranger things</li><li>Friends</li><li>Sherlock</li></ul><p><strong>ROUND 1: </strong>Pen and paper quiz</p><p>Number of Questions: 25 questions</p><p>Duration: 40 mins</p><p><strong>ROUND 2: Ears at play!</strong></p><ul><li>The audio of a particular scene will be played.</li><li>The team has to identify which series it belongs to, which season, the speaker, the listener and the context of the scene.</li><li>3 teams will be selected from this round</li></ul><p><strong>ROUND 3: Finals (Dumb charades)</strong></p><ul><li>Player 1 has to act the word given.</li><li>Player 2 should correctly identify the word.</li></ul>","<p>Teams of 2 members are allowed</p><ul><li>Instructions given must be strictly followed</li><li>Use of mobile phones is not allowed</li><li>Teams consisting of members from different college is allowed</li><li>Decisions taken by Organizers are final and binding</li></ul>","<p>PAVITHRA.C : 8870603612</p><br/><p>GOWARDHINI.S : 9486172707</p>"]},{"title":"Gamindrome","content":["<p><i>&ldquo;I don&rsquo;t need to get a life, I&rsquo;m a gamer, I have lots of lives!&rdquo;</i></p><p>Is WASD what moves you? Then don&rsquo;t get an NPC &ndash; get moving!</p><p>Clash Royale. FIFA. Call of Duty.</p><p>Three events. Three winners. One event.</p><p>Lock and Load Sarge. Final Boss Abacus has been located. Fire at will.</p>","<p><strong>1) CLASH ROYALE</strong></p><p><strong>FORMAT: 3 rounds</strong></p><p>ROUND 1: Elimination</p><ul><li>The players have to be in the same clan and a 1 vs 1 friendly battle will take place.</li><li>The battle deck should contain 6 commons, 1 rare, 1 epic, no legendary</li><li>Each round has 3 stages. The clasher with most wins advances to the next stage.</li></ul><p>ROUND 2: Qualifier</p><ul><li>Clashers who passed previous round will now battle.</li><li>The battle deck should contain 5 commons, 2 rares, 1 epic, no legendary</li></ul><p>ROUND 3: Finals</p><ul><li>The battle deck should contain 4 commons, 2 rares, 2 epics, no legendary (1 epic may be substituted with 1 legendary if both players have unlocked them)</li><li>Battles will be conducted among the players till the ultimate clasher persists!</li></ul><p><strong>2) FIFA</strong></p><p><strong>Points System:</strong></p><p>The following point system will be in place during all matches. The point system is based on the following parameters,</p><ol><li>Victory/Loss</li><li>Goal Difference</li><li>Clean Sheet &ndash; 6pts</li><li>Pass Accuracy &ndash;</li><li>above 90% : 3pts</li><li>80 &ndash; 90%: 2pts</li><li>75 &ndash; 80%: 1pts</li><li>Goals scored &ndash; 4 or more : 3pts</li><li>Goals conceded &ndash; 2 or more: - 2pts</li></ol><p>Total points will be sum of the conditions 2 &ndash; 6.</p><p><strong>Round 1:</strong></p><ol><li>The Top 8 will be selected based on the points system.</li><li>The events will be based on rules alone.</li></ol><p><strong>Round 2:</strong></p><ol><li>Top 8 from previous round will play in knockout round.</li><li>This round is direct elimination.</li><li>Points system is not applicable in this round.</li><li>The contestant can choose any team of their wish.</li></ol><p><strong>3) CALL OF DUTY</strong></p><p><strong>ROUND 1: </strong>Knife round</p><p>Team that wins in knife round will get to choose the map for the 1st match</p><p><strong>ROUND 2: </strong>Search and destroy</p><p>Team that wins chooses next map</p><p><strong>ROUND 3: Search and destroy</strong></p><p>Winning team chooses the map</p><p><strong>ROUND 4: Final round</strong></p><p>Team death match</p>","<p><strong>1) CLASH ROYALE</strong></p><ul><li>Battle members will be assigned by the organisers only.</li><li>Mobile phones are mandatory and must be brought by the player.</li><li>Come with a good internet connectivity. If any lag occurs, a rematch may not be acceptable.</li><li>Points will be awarded based on number of crowns won.</li></ul><p><strong>2) FIFA</strong></p><ol><li>Opponent will choose your team based on the teams provided with equal stats.</li><li>Half &ndash; Time: 5 minutes.</li><li>Points will be updated in front of the contestant.</li></ol><p><strong>3) CALL OF DUTY</strong></p><ul><li>Come as a team (3 - 4 members) if possible.</li><li>Perks &ndash; only normal weapons.</li><li>Mods will not be allowed.</li><li>Team that wins maximum among 2, 3, and 4 will be selected for further rounds.</li><li>Points will be granted based on the number of kills and deaths.</li></ul>","<p><strong>1) CLASH ROYALE</strong></p><p>VENUGOPAL S 9487060227</p><p><strong>2) FIFA</strong></p><p>MUGILAN G 8189891645</p><p>JEGADEESH M 9677615641</p><p>PRAKASH KUMAR K 9994170538</p><p><strong>3) CALL OF DUTY</strong></p><p>VENUGOPAL S 9487060227</p><p>HARINATH 9940364209</p>"]},{"title":"Finding Nemo","content":["<p>&lsquo;<em>When life gets you down, do you wanna know what you gotta do? Just keep Swimming, just keep swimming, just keep swimming.</em>&rsquo;</p><p style='text-align:right'>-Dory, Finding Nemo</p><p>Enjoy the more gentle streams of Abacus in this fun event! Decipher the clues and reach your goal. And if you get lost in between, just keep swimming!</p>","<p><strong>Round 1: Pen and Paper</strong></p><ul><li>Number of Questions: 10 questions</li><li>Questions of varying difficulty will be placed in and around the department.</li><li>All questions are encrypted. Once you have successfully decrypted the question, you must complete the task that follows.</li><li>The top 4 teams will be selected for round 2</li></ul><p><strong>Round 2:</strong></p><ul><li>One participant will be given clues to a particular following question.</li><li>Once the participant has solved the clues, he/she must pass it to their team mates via SMS.</li><li>Number of Questions: 5</li><li>Duration: 30 mins</li></ul>","<ol><li>Only teams of 2 or 3 are allowed.</li><li>Instructions should be followed strictly</li><li>Mobile phone is necessary for round-2</li><li>Teams consisting of members from different departments/colleges are allowed</li><li>Decisions taken by the organisers are final and binding</li></ol>","<p>Subha : 95971 21366</p><p>Lalitha : 9176390419</p><p>Jenifer : 9790718543</p>"]},{"title":"Houseful","content":["<p>If codes and algorithms are giving you a run for your money, then take a breather with us at Houseful. How many cuts must you make on a piece of paper to get a circle? How fast can you stack and unstack 20 cups? How many brain teasers can you take before you&rsquo;re stuck scratching your heads?</p>","<p><strong>Round 1:</strong></p><ul><ul><li>Number of Questions: 30</li><li>Questions will be based on movies, cricket, connections, puzzles etc.</li><li>Duration: 40 mins</li></ul></ul><p><strong>Round 2:</strong></p><ul><ul><li>Fun tasks will be given</li><li>Each task will have time limit</li><li>7- 10 teams will selected for this round</li></ul></ul><p><strong>Round 3 (Final Round):</strong></p><ul><ul><li>Creative + Fun tasks will be given</li><li>Each task will have time limit</li><li>3 -5 teams will selected for this round</li></ul></ul>","<ol><li>Only 2 or 3 members per team allowed</li><li>Instructions given must be strictly followed</li><li>Use of mobile phones is not allowed</li><li>Teams consisting of members from different college is allowed</li><li>Decisions taken by Organizers are final and binding</li></ol>","<p>NIVETHA .K &ndash; 9551238717</p><p>PRADEEPA .A - 9698111129</p>"]},{"title":"Alcatraz","content":["You break the rules, you go to prison. You break prison rules, you go to Alcatraz. Ladies and Gentlemen, welcome to Alcatraz. The clock is ticking and the walls are closing in. Do you have what it takes to break the impregnable?","<p><strong>Round 1: Pen and Paper </strong></p><ul><li>The participants will be given logical and aptitude questions of different levels of difficulty</li><li>Number of questions: 25 questions.</li><li>Duration: 30 minutes</li></ul><p>&nbsp;</p><p><strong>Round 2 : Prison break</strong></p><ul><li>Participants will be locked in a room with several clues. Given a scenario, participants will be asked to use the clues to find their way out.</li><li>Duration: 1 hour</li></ul>","<ol><li>Maximum three per team. Lone wolves allowed.</li><li>Teams consisting of participants from different colleges allowed.</li><li>Any form of malpractice will lead to immediate disqualification.</li><li>Decisions taken by organizer are final and binding.</li></ol>","Abaian    : 9047863210<br/>Kousalya : 9715203020"]},{"title":"கற்றது தமிழ்","content":["<div>'எங்கும் தமிழே முழங்க கேட்போம்&nbsp;</div><div>எதிலும் தமிழே வழங்க கேட்போம் பொங்கும் தமிழின் இலக்கிய சுவையை பூமியின் மீது உலவ விடுவோம்'</div><div>திமிலை பற்றிக்கொண்டு விளையாடுபவனுக்கு தமிழின் சிறப்பை போற்றிக்கொண்டு விளையாட வாய்ப்பு இதோ!!! </div>","<div><strong>போட்டி வடிவம் :-</strong> 2 சுற்றுகள் கொண்டது.</div><p>&nbsp;</p><div><strong>சுற்று 1:-</strong> உதடு உச்சரிப்பினால் தமிழை உணர்தல்.</div><p>&nbsp;</p><div>காகிதங்கள் பல குவியலாக கண் முன் காண்பீர்கள்.அக்காகிதங்களில் தமிழில் திருக்குறள் பழமொழி தமிழ்வார்த்தை என பல எழுதப்பட்டிருக்கும்.உங்கள் அணியில் ஒருவர் நீங்கள் சொல்லும் வார்த்தை செவிக்கெட்டாவண்ணம் காதில் பாடல் கேட்டுக்கொண்டிருப்பார்.காகிதத்<wbr />தில் உள்ள வார்த்தையை நீங்கள் சத்தமாக படிக்க உங்களின் உதட்டின் அசைவை கொண்டு தமிழ்வார்த்தையை மற்றொருவர் கண்டுபிடிக்க வேண்டும்.நம் தமிழில் விளையாட வாய்ப்பு உங்கள் அணியின் மற்றொரு நபரின் வாயில் உள்ளது,விளையாடுங்கள்!</div><p>&nbsp;</p><div><strong>சுற்று 2:-</strong> படங்களை இணைத்து கதாசிரியர் ஆகுங்கள்.</div><p>&nbsp;</p><div>முதல் சுற்றில் வெற்றிபெற்றவர்கள் இரண்டாம் சுற்றில் கொடுக்கப்பட்ட ஐந்து வெவ்வேறு தமிழ் சொற்களைக் கொண்டு ஒரு கதையினை கூற வேண்டும்.இயன்ற வரை ஆங்கில வார்த்தை கலக்காமல் கதை கூறும் அணிக்கு வெற்றி கிட்டும்.</div>","<ol><li>ஒர் அணியில் இருவர் மட்டுமே அனுமதிக்கப்படுவர்.</li><li>நடுவரின் தீர்ப்பே இறுதியானது</li><li>சைகையின் மூலம் பதிலளித்தல் கூடாது.</li><li>ஒவ்வொரு அணிக்கும் முதல் சுற்றில் 1 நிமிடமும் இரண்டாவது சுற்றில் 5 நிமிடங்கள் மட்டுமே வழங்கப்படும்.</li><li>இரண்டாவது சுற்றில் கதையின் சுவாரசியத்தைப் பொருத்தே மதிப்பெண் வழங்கப்படும்.ஆங்கில வார்த்தைகள் பயன்படுத்தினால் மதிப்பெண்கள் குறைக்கப்படும்.</li></ol>","<p>Cibi Chakravarthy S - 94862 93823</p><p>Bavya V - 87542 49646</p><p>Mahadevan K - 82206 39582</p><p>Sujitha - 99444 61560</p>"]},{"title":"Marketing Mania","content":["Tired of watching the same old ads on TV? Have you ever felt that you could come up with far better advertisements? Then summon the marketing guru in you. Use your way with words to sweet talk the crowd into believing that yours is the next hottest brand in town!","<p><strong>Round 1: Adzap</strong></p><p>&nbsp;2 minutes for preparation and 3 minutes for presenting (total 5 minutes per team)</p><p><strong>Round 2: Adzap</strong></p><p>3 minutes for preparation and 5 minutes for presenting (total 8 minutes per team)</p>","<ol><li>Each team should comprise of 3-4 members only.</li><li>Topics will be given on spot.</li><li>Performance is judged based on co-operation and creativity.</li><li>Execution may be either Tamil or English.</li><li>Audience decision is final and binding.</li></ol>","<p>B.Priyadharshini - 9840460971</p><p>S.Nithisha - 9551352454</p><p>A.Vishali - 9791074362</p>"]},{"title":"randomize()","content":["<p>If everyday life has got you in a rut, get ready, things are about to take a massive turn! From connections to brain teasers to so much more, Abacus&rsquo; Randomize is here to give you a run for your money. Say goodbye to those daily doldrums, things are about to get random! </p>","<p><strong>Round 1: Connections (Pen and Paper)</strong></p><ul><li>Number of Questions: Approx. 30 questions.</li><li>Duration: 30 minutes</li></ul><p><strong>Round 2: Pen and Paper</strong></p><ul><li>The participants will be given a series of brain teasers and puzzles.</li><li>Number of Questions: Approx. 30 questions.</li><li>Duration: 45 minutes</li></ul>","<ol><li>Maximum three per team. Lone wolves allowed.</li><li>Teams consisting of participants from different colleges allowed.</li><li>Any form of malpractice will lead to immediate disqualification.</li><li>Decisions taken by organizer are final and binding.</li></ol>","<p>Santosh : 96558 32644</p><p>Nandhini : 94448 33190</p><p>Movina : 97899 35916</p>"]},{"title":"Spin to Win","content":["<p>If you&rsquo;re feeling lucky, then test your fortune at Abacus&rsquo; Spin to Win. Be nimble and quick on your toes, you&rsquo;ve got to race against the clock. Complete the task and cash in your win with exciting goodies!</p>","<p>2 Rounds.<br /><strong>Round 1:</strong></p><ul><li>Tasks based on movie , cricket , puzzles etc. will be given.</li><li>Each task will have time limit.</li></ul>","<ol><li>Only 2 to 3 members per team allowed</li><li>Instruction given must be strictly followed</li><li>Usage of mobile phones is not allowed</li><li>Team consisting of members from different college is allowed</li><li>Decision taken by organizers are final and binding.</li></ol>","<p>Revathi - 8883102008</p><p>Vani - 8056509620</p>"]},{"title":"General Quiz","content":["<p>Are your mornings incomplete without a cup of coffee and a newspaper fresh off the press? Are you the &ldquo;Google&rdquo; of your gang? Do you enjoy reading about anything and everything under the sun? Put that knowledge to good use and come join the Abacus GenQuiz!</p>","<p><strong>Round 1:</strong></p><ul><li>Number of Questions: 25 questions (5 tie breaker)</li><li>Duration: 30 minutes.</li><li>Includes audio/ video questions.</li></ul><p><strong>Round 2:</strong></p><ul><li>6 teams will be shortlisted to this round</li><li>Typical quiz style format will be followed.</li></ul>","<ol><li>Teams of two can participate. Lone wolves are NOT allowed.</li><li>For prelims, the questions will be displayed, the answers to which will have to be answered on a paper.</li><li>Use of internet during the quiz is strictly prohibited.</li><li>If a team is found to be helping another team, both the teams will be immediately disqualified.</li><li>The format for the finals will be declared on the spot.</li><li>The quiz master's decision will be final and binding.</li></ol>","<p>Sivaram Kumar - 8220762765</p><p>Monish - 9952055048</p>"]},{"title":"Bid Battle","content":["<p>Think you known heads and tail of cricket? Good. What about money management? Even better. Come and join many other cricket fanatics to see who is the next Srini Mama or a SRK.</p>","<p><strong>Round 1: Pen and Paper</strong></p><ul><li>Number of Questions: 30</li><li>Duration: 30 minutes</li><li>The participants will be given 20 questions related to cricket, ranging from the old test cricket to current formats, including d T10.</li></ul><p><strong>Round 2: Real Auction</strong></p><ul><li>IPL auction style, where you get franchises and players, which tests your money management skills and how good you take players in your team</li><li>Duration: Around 3 hours</li></ul>","<ol><li>2-3 people in a team allowed.</li><li>Teams consisting of participants from different colleges allowed.</li><li>Any form of malpractice will lead to immediate disqualification.</li><li>Decisions taken by organizer are final and binding.</li></ol>","<p>Prasanna - 9003222748</p>"]}]

